import React from 'react'
import styled from 'styled-components'
import Modal from '../Modal'
import { Text } from 'rebass'
import { CloseIcon } from '../../theme/components'
import { RowBetween } from '../Row'
import { AutoColumn } from '../Column'
import { useNoticeInfoToggle, useNoticeInfoVisible } from '../../state/user/hooks'
import { useTranslation } from 'react-i18next'

const Wrapper = styled.div`
  width: 100%;
  max-width: 420px;
`
const Section = styled(AutoColumn)`
  padding: 24px;
`

function ConfirmationContent({ onDismiss }: { onDismiss: () => void }) {
  const { t } = useTranslation()

  return (
    <Wrapper>
      <Section>
        <RowBetween>
          <Text fontWeight={500} fontSize={16}>
            {t('Notice')}
          </Text>
          <CloseIcon onClick={onDismiss} />
        </RowBetween>
        <AutoColumn gap="12px" justify={'center'}>
          <Text fontWeight={500} fontSize={20} margin={10}>
            {t('This is a notice content.')}
          </Text>
        </AutoColumn>
      </Section>
    </Wrapper>
  )
}

export default function NoticeModal() {
  const toggleNoticeInfo = useNoticeInfoToggle()
  const showNoticeInfo = useNoticeInfoVisible()

  return (
    <Modal isOpen={!showNoticeInfo} onDismiss={toggleNoticeInfo} maxHeight={90}>
      <ConfirmationContent onDismiss={toggleNoticeInfo} />
    </Modal>
  )
}
